import React, {Component} from "react";
import axios from 'axios';
import GoogleLoginBtnComp from "./GoogleLoginBtnComp";
import GoogleLogoutBtnComp from "./GoogleLogoutBtnComp";
import store from "../../../redux/store";
import { URL, actionType, mainViewType } from "../../../redux/config";
import NaverLoginBtnComp from "./NaverLoginBtnComp";
import FaceBookLoginBtnComp from "./FaceBookLoginBtnComp";
import KakaoLoginBtnComp from "./KakaoLoginBtnComp";
import KakaoLoginBtnComp3 from "./KakaoLoginBtnComp3";

import TextField from "@material-ui/core/TextField";

import MakeLoginBtn from "./MakeLoginBtn";
import { FaThemeisle } from "react-icons/fa";
import {Route, Link, withRouter } from "react-router-dom";

class LoginPageComp extends Component {

    constructor(props) {
        super(props);
        console.log("LoginPageComp constructor", props);
        
        this.state={
            id:'',  //   state
            pass:'',
            pwCheck: "",//  
            name : "",
            birth_date: "",
            phone : "",
            email : "",
            address : "",
            idcanUse: false,//  true 
            logged:this.props.logged,
            onLogin:this.onLogin,
        }

        // 
    }

    setLoginId = (loginId) => {
        console.log("LoginPage setLoginId()");
        store.dispatch({
            type: actionType.LOG_IN,
            // mainView: mainViewType.MainPage
            loginId: loginId,
            logged: true
        });
    }

    //   state      
    //   setState   
    // 
    changeEvent=(e)=>{
        
        // console.log(e.target.id+":"+e.target.value);
        //      
        this.setState({
            [e.target.name]:e.target.value
        })
    }

    onLogin=()=>{
        // console.log("  " + this.state.id + " " + this.state.pass);
        const data = {
            id: this.state.id,
            pass: this.state.pass
        }
        let url = URL + "/member/login";

        axios.post(url, data)
        .then(response => {
            console.log(response.data);
            if(response.data){
                // this.props.onLogin();
                this.setLoginId(data.id);
                // alert(store.getState().loginId+ "   ");
                this.props.history.push("/");
            }
            else{
                alert("  .");
                this.setState({
                    pass: '',
                })
            }
        }).catch(err => {
            console.log(" :"+err);
        })
    }

    render() {
        // console.log("LoginPageComp render()", this.props);
        // console.log("     : " + store.getState().loginId);
        const parentOnLoginHandler = this.onLogin.bind(this);
        return (
            <div>
                <h4></h4>
                <div>
                    {this.state.id}
                    <h4 className="showIdResult">{this.state.showIdResult}</h4>
                </div>
                <br />
                
                <TextField id="standard-secondary" label="" color="secondary" 
                type="text" name="id" value={ this.state.id }
                onChange = { this.changeEvent.bind(this) } />

                <br />

                <TextField id="standard-secondary" label="" color="secondary" 
                type="password" name="pass" value={ this.state.pass }
                onChange = { this.changeEvent.bind(this) } />

                <br />
                <button type="button"
                onClick={this.onLogin.bind(this)}>
                    Sign in
                </button>
                <MakeLoginBtn 
                parentOnLoginHandler={parentOnLoginHandler} />
                <h5>
                      {this.state.id} 
                      {this.state.pass} 
                </h5>
                {/*  */}
                <Link to="/join"><button type="button" className="btn btn-warning"></button></Link>
                
                
                <GoogleLoginBtnComp />
                <GoogleLogoutBtnComp />
                <br />
                <br />
                <NaverLoginBtnComp />
                <br />
                <FaceBookLoginBtnComp />
                <br />
                {/* <KakaoLoginBtnComp /> */}
                <br />
                <KakaoLoginBtnComp3 />
            </div>
        )
    }

}

export default withRouter(LoginPageComp);
