/*global kakao */
import React, { useEffect, useState} from "react";
import './kakaomap.css';
import Box from '@material-ui/core/Box';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import RestaurantIcon from '@material-ui/icons/Restaurant';
import LocalCafeIcon from '@material-ui/icons/LocalCafe';
import HotelIcon from '@material-ui/icons/Hotel';
import AroundModal from "./AroundModal";
import UseModal from './UseModal';

const MapComp=(props)=> {

    let longitude = useState(0);
    let latitude = useState(0);
    let title = useState("");
    const [category, setCategory] = useState("");
    const [wishTitle, setWishTitle] = useState("");
    const [wishContent, setWishContent] = useState("");
    
    const [value, setValue] = React.useState(0);

    console.log("longitude : " + props.longitude);
    useEffect(() => {
        
        longitude = props.longitude;
        latitude = props.latitude;
        title = props.title;

        mapscript();
    });

  const mapscript = () => {
    
    //let markers = [];
    let placeOverlay = new kakao.maps.CustomOverlay({zIndex:1}), 
        contentNode = document.createElement('div'), //      
        markers = [], //   
        currCategory = '';


    let mapContainer = document.getElementById("map"), mapOption = {
        center: new kakao.maps.LatLng(latitude, longitude), //  
        level: 3 //   
    };

    const map = new kakao.maps.Map(mapContainer, mapOption);
    let ps = new kakao.maps.services.Places(map);
    
    //   
    let markerPosition = new kakao.maps.LatLng(
        latitude,
        longitude
    );

    //  
    let marker = new kakao.maps.Marker({
        map:map,
      position: markerPosition,
      title: title
    });

    let content = '<div className="wrap"><div className="info"><div className="mtitle">' + title
            + '<div className="close" onClick={this.closeOverlay.bind(this)} title=""></div></div></div></div>';

    //   
    //     CSS   
    let overlay = new kakao.maps.CustomOverlay({
        content: content,
        map: map,
        position: marker.getPosition()       
    });
    //     
    kakao.maps.event.addListener(marker, 'click', function() {
        overlay.setMap(map);
    });
    //          
    let infowindow = new kakao.maps.InfoWindow({zIndex:1});

    //      
    function closeOverlay() {
        overlay.setMap(null);     
    }
    //  idle  
    kakao.maps.event.addListener(map, 'idle', searchPlaces);
    //     css class  
    contentNode.className = 'placeinfo_wrap';
    //     mousedown, touchstart  
    //        kakao.maps.event.preventMap   
    addEventHandle(contentNode, 'mousedown', kakao.maps.event.preventMap);
    addEventHandle(contentNode, 'touchstart', kakao.maps.event.preventMap);
    //    
    placeOverlay.setContent(contentNode);  
    //     
    addCategoryClickEvent();   
    
    //searchPlaces();

    function addEventHandle(target, type, callback) {
        if (target.addEventListener) {
            target.addEventListener(type, callback);
        } else {
            target.attachEvent('on' + type, callback);
        }
    }
    //    
    function searchPlaces() {
        if (!currCategory) {
            return;
        }
        
        //    
        placeOverlay.setMap(null);
        //     
        removeMarker();
        
        ps.categorySearch(currCategory, placesSearchCB, {useMapBounds:true});
        
    }
    //      
function placesSearchCB(data, status, pagination) {

    if (status === kakao.maps.services.Status.OK) {
         //      

         displayPlaces(data);
         
      //   
         displayPagination(pagination);
     } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
         //        
         displayPlaces(null);
     } else if (status === kakao.maps.services.Status.ERROR) {
         //            
         
     }
 }
 //      
 function displayPlaces(places) {
    //     
     //       
     var order = document.getElementById(currCategory).getAttribute('data-order');
     var listEl = document.getElementById('placesList'), 
     menuEl = document.getElementById('menu_wrap'),
     fragment = document.createDocumentFragment(), 
     bounds = new kakao.maps.LatLngBounds(), 
     listStr = '';
  //      
     removeAllChildNods(listEl);
     //     
     removeMarker();
     
     if(places == null){
        itemEl = getListItem(-1, null);
        fragment.appendChild(itemEl);
     }
    else{
        for ( var i=0; i<places.length; i++ ) {
            //    
            var placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
                marker = addMarker(placePosition, i), 
                itemEl = getListItem(i, places[i]); //    Element 
            //       
            // LatLngBounds   
            bounds.extend(placePosition);
            //    mouseover 
            //     
            // mouseout    
            (function(marker, place) {
                kakao.maps.event.addListener(marker, 'click', function() {
                    displayPlaceInfo(place);
                });
            })(marker, places[i]);
            fragment.appendChild(itemEl);
        }
    }
    
     //     Elemnet 
     listEl.appendChild(fragment);
    // menuEl.scrollTop = 0;
     //       
     //map.setBounds(bounds);
 }
 //   Element  
 function getListItem(index, places) {
     
     if(index == -1){
        var el = document.createElement('li'),
        itemStr = "<div><br/>   .</div>";
        el.innerHTML = itemStr;
        el.className = 'item';
    
        return el;
     }
    else{
        // setting(places);
        var el = document.createElement('li'),
        itemStr = '<table className="table table-bordered" id="placeListTable"><tr>' +
                    '<td style={{width:"20%"}}><span class="markerbg marker_' + (index+1) + '"></span></td>' +
                    '<td style={{width:"60%"}}><a class="info" href="' + places.place_url + '" target="_blank" title="' + places.place_name + '">' +
                    '<p>' + places.place_name + '</p>';
        itemStr += '<span class="gray">' +  places.address_name  + '</span>';      
        itemStr += '<span class="tel">' + places.phone  + '</span>' + 
                    '</a></td>' +
                    '<td style={{width:"20%"}}><div class="addWishBtn"></div></td></tr></table>';
        el.innerHTML = itemStr;
        el.onclick=function(){
            setWishTitle(places.place_name);
            setWishContent(places.address_name);
            toggle();
        }
        el.className = 'item';
                
        return el;

        // return <div dangerouslySetInnerHTML={{ __html : itemStr}}></div>;
    }    
 }
 
 
 
 //       
 function addMarker(position, idx, title) {
     var imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', //   url,   
         imageSize = new kakao.maps.Size(36, 37),  //   
         imgOptions =  {
             spriteSize : new kakao.maps.Size(36, 691), //   
             spriteOrigin : new kakao.maps.Point(0, (idx*46)+10), //       
             offset: new kakao.maps.Point(13, 37) //      
         },
         markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
             marker = new kakao.maps.Marker({
             position: position, //  
             image: markerImage 
         });
     marker.setMap(map); //    
     markers.push(marker);  //    
     return marker;
 }
 //       
 function removeMarker() {
     for ( var i = 0; i < markers.length; i++ ) {
         markers[i].setMap(null);
     }   
     markers = [];
 }
 //      
 function displayPagination(pagination) {
     var paginationEl = document.getElementById('pagination'),
         fragment = document.createDocumentFragment(),
         i; 
     //    
     while (paginationEl.hasChildNodes()) {
         paginationEl.removeChild (paginationEl.lastChild);
     }
     for (i=1; i<=pagination.last; i++) {
         var el = document.createElement('a');
         el.href = "#";
         el.innerHTML = i;
         if (i===pagination.current) {
             el.className = 'on';
         } else {
             el.onclick = (function(i) {
                 return function() {
                     pagination.gotoPage(i);
                 }
             })(i);
         }
         fragment.appendChild(el);
     }
     paginationEl.appendChild(fragment);
 }

    //         
    function displayPlaceInfo (place) {
        var content = '<div class="placeinfo">' +
                        '   <a class="title" href="' + place.place_url + '" target="_blank" title="' + place.place_name + '">' + place.place_name + '</a>';   

        if (place.road_address_name) {
            content += '    <span title="' + place.road_address_name + '">' + place.road_address_name + '</span>' +
                        '  <span class="jibun" title="' + place.address_name + '">( : ' + place.address_name + ')</span>';
        }  else {
            content += '    <span title="' + place.address_name + '">' + place.address_name + '</span>';
        }                
    
        content += '    <span class="tel">' + place.phone + '</span>' + 
                    '</div>' + 
                    '<div class="after"></div>';

        contentNode.innerHTML = content;
        placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
        placeOverlay.setMap(map);  
    }

    //    Element  
    function removeAllChildNods(el) {   
        while (el.hasChildNodes()) {
            el.removeChild (el.lastChild);
        }
    }
  
    //    
    function addCategoryClickEvent() {
        var category = document.getElementById('category'),
            children = category.children;
        for (var i=0; i<children.length; i++) {
            children[i].onclick = onClickCategory;
        }
    }

    //    
    function onClickCategory() {
        var id = this.id, className = this.className;

        placeOverlay.setMap(null);
        if (className === 'on') {
            currCategory = '';
            // changeCategoryClass();
            removeMarker();
        } else {
            currCategory = id;
            setCategory(currCategory);
            // changeCategoryClass(this);
            searchPlaces();
        }
    }

    //     
    // function changeCategoryClass(el) {
    //     var category = document.getElementById('category'),
    //         children = category.children,
    //         i;
    //     for ( i=0; i<children.length; i++ ) {
    //         children[i].className = '';
    //     }
    //     if (el) {
    //         el.className = 'on';
    //     } 
    // } 
    //    
    marker.setMap(map);
  };


    const {isShowing, toggle} = UseModal();
  return (
      <div>
          {/* <div id="map" style={{ width: "500px", height: "500px" }}></div> */}
          
          {/* <button className="button-default" onClick={toggle}>Show Modal</button> */}
          {/*  */}
          <AroundModal
                isShowing={isShowing}
                hide={toggle}
                wishTitle = {wishTitle}
                wishContent = {wishContent}
                category = {category}
            />

          <Box className="map_wrap" style={{textAlign: 'center'}}
                        display="flex"
                        flexWrap="wrap"
                        p={1}
                        m={1}
                        bgcolor="background.paper"
                        justifyContent="center"
                        css={{ maxWidth: '100%' }}
                    >
                        
                        <Box m={1} id="map" style={{position:'relative', overflow:'hidden'}}>
                            <input type="hidden" value={title} id="keyword" size="15"></input>
                        </Box>

                        <br/><br/>
                        
                        <Box m={1} id="placeListBox">
                            <BottomNavigation
                                    id="category"
                                    value={value}
                                    onChange={(event, newValue) => {
                                        setValue(newValue);
                                    }}
                                    showLabels
                                    >
                                    <BottomNavigationAction id="FD6" data-order="0" label="Food" icon={<RestaurantIcon />} 
                                        style={{borderLeft: '1px solid #ddd', borderTop: '1px solid #ddd', borderBottom: '1px solid #ddd'}} />
                                    <BottomNavigationAction id="CE7" data-order="1" label="Cafe" icon={<LocalCafeIcon />} style={{border: '1px solid #ddd'}} />
                                    <BottomNavigationAction id="AD5" data-order="2" label="Rooms" icon={<HotelIcon />}
                                        style={{borderRight: '1px solid #ddd', borderTop: '1px solid #ddd', borderBottom: '1px solid #ddd'}}/>
                                    </BottomNavigation>
                            <div id="menu_wrap" class="bg_white">
                                <ul id="placesList">
                                    <li style={{marginTop: '30px'}}>  .</li>
                                </ul>
                                <div id="pagination"></div>
                            </div>
                        </Box>
                        
                    </Box>

                    {/*    */}
                    {/* <Modal
                        aria-labelledby="transition-modal-title"
                        aria-describedby="transition-modal-description"
                        className="aroundmodal"
                        open={open}
                        onClose={handleClose}
                        closeAfterTransition
                        BackdropComponent={Backdrop}
                        BackdropProps={{
                        timeout: 500,
                        }}
                    >
                        <Fade in={open}>
                        <div className="aroundpaper">
                            <span className="aroundmodalTitle"> </span><br/> */}
                            {/* &nbsp;&nbsp;{this.state.spotdata.title}<br/> */}
                            {/* &nbsp;&nbsp;{this.state.spotdata.roadaddr}<br/> */}
                            {/* &nbsp;&nbsp; 
                            <input type="date" class="form-control form-control-sm" ref="wishday"></input>
                            &nbsp;&nbsp; 
                            <input type="time" class="form-control form-control-sm" ref="wishtime"></input><br/>
                            <div style={{textAlign: 'center'}}>
                                <button type="button" class="btn btn-warning aroundmodalBtn"><b></b></button>
                            </div>

                        </div>
                        </Fade>
                    </Modal> */}

                    
      </div>   
  );
}

export default MapComp;