import React , { Component } from 'react';
import reactDOM from 'react-dom';
import GoogleLogin from 'react-google-login';
import { refreshTokenSetup } from "./refreshToken";
import store from "../../../redux/store";
import { URL, actionType, mainViewType } from "../../../redux/config";

class GoogleLoginBtnComp extends Component
{
    //     state id, name, provider 
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            name: '',
            provider: '',
            accessToken: ''
        }
    }

    setLoginId = (loginId) => {
        console.log("LoginPage setLoginId()");
        store.dispatch({
            type: actionType.LOG_IN,
            // mainView: mainViewType.MainPage
            loginId: loginId
        });
    }

    setGoogleOn = () => {
        console.log(" ");
        store.dispatch({
            type: actionType.googleLogin,
            googleOn: true
        });
    }

    render() {
        const responseGoogle = (response) => {
            console.log(response);
        }
        
        //   json  => onSuccess state id, name 
        const onSuccess = (res) => {
            console.log('[Login Success]  :', res.profileObj);
            console.log('  id : ' +  res.googleId);

            this.setState({
                id: res.googleId,
                name: res.profileObj.name,
                provider: 'google'
            });

            this.setGoogleOn();

            alert("    : " + store.getState().googleOn);

            // initializing the setup
            refreshTokenSetup(res);
        };

        //      => onFailure error 
        const onFailure = (res) => {
            console.log('[Login failed] res:', res);
        };

        return (
            <>
            <br />
            <br />
            <GoogleLogin
                clientId="256166181377-83u2uuteqgosooa3um2i3o36ho1325md.apps.googleusercontent.com"
                buttonText="Login"
                onSuccess={onSuccess}
                onFailure={onFailure}
                cookiePolicy={'single_host_origin'}
                isSignedIn={true}
            />
            </>
        );
    }
}

export default GoogleLoginBtnComp;